import React from "react"
import "./Nav.css"


export default class Nav extends React.Component{
    constructor(){
        super()
        this.passToAsideValue=this.passToAsideValue.bind(this)
        this.state={
            navList:[{param:"c1",content:"例子",className:"li1Active"},
            {param:"c2",content:"积分兑换",className:""},
            {param:"c3",content:"积分管理",className:""},
            {param:"c4",content:"商户",className:""},
            {param:"c5",content:"系统管理",className:""}
        ]
        }
    }
    //点击导航切换选项卡
    passToAsideValue(claName,e){
        var sad=document.getElementById("secAside");
        sad.dataset.claName=claName;
        var oDivs=sad.getElementsByTagName("div");
            for(var item of oDivs){
                item.style.display="none";
                if(sad.dataset.claName==item.className){
                    item.style.display="block";
                    e.target.style.textShadow="0 0 0 none";
                }
            }
            e.target.parentElement.children[0].className="";
           for(var item1 of e.target.parentElement.children){
            item1.style.background="";
            item1.style.color="black";
            item1.style.textShadow="";
           }
        e.target.style.background="green";
        e.target.style.color="white";
        e.target.style.textShadow="-1px -1px black";
    }
    render(){
        return (
            <div id="nav" >
                <ul className="navbar">
                {
                    this.state.navList.map((item,i)=>{
                        return (<li key={i} className={item.className} onClick={(e)=>{this.passToAsideValue(item.param,e)}}>{item.content}</li>)
                    })
                }
                    
                </ul>
                
                <div>xxxxxxxx页</div>
            </div>
        )
    }
}

