import React from "react"
import DateComp from "./DateComp";
import "./Edit.css"
import { DatePicker } from '@progress/kendo-react-dateinputs';
export default class Edit extends React.Component {

    min = new Date(2000, 2, 10);
    max = new Date(2002, 2, 10);
    defaultValue = new Date();

    constructor(){
        super()
        this.state={

        }
    }
    componentDidMount(){
        var oEdit=document.getElementById("edit");
        oEdit.style.width=document.documentElement.clientWidth+"px";
        oEdit.style.height=window.innerHeight+"px";
        
        oEdit.style.position="fixed";
        oEdit.style.top=0;
        oEdit.style.left=0;
    }
    editHide(){
        document.getElementById("edit").style.display="none";
    }
    render() {
        return (
            <div id="edit">
                <div className="editContainer">
                
                    <div><span><img src={require("./../../../images/plus.png")}/><span>编辑</span></span><span onClick={()=>{this.editHide()}}><img src={require("./../../../images/cancel.png")}/></span></div>
                    <div><div><span>赠送积分项目</span><input placeholder="赠送信贷客户"/></div><div><span>赠送积分分值</span><input placeholder="10000"/></div></div>
                    <div><div><span>过期时间</span><p className="p2"><DatePicker min={this.min}
                        max={this.max}
                        defaultValue={this.defaultValue}/></p></div><div><span>赠送日期</span><p className="p2"><DatePicker min={this.min}
                        max={this.max}
                        defaultValue={this.defaultValue}/></p></div></div>
                    <div><div><span>赠送说明</span><input placeholder="介绍成功"/></div><div><span>已经使用积分</span><input placeholder="0"/></div></div>
                    <div><button onClick={()=>{this.editHide()}} className="cancel">取消</button><button className="submit">提交</button></div>
                </div>
                
            </div>
        )
    }

    
}