
import React from "react"
import SecAside from "./SecComponents/SecAside"
import SecSection from "./SecComponents/SecSection"
import "./Section.css"
export default class Section extends React.Component{
    constructor(){
        super()
        this.doGetSecAsideList=this.doGetSecAsideList.bind(this)
        this.state={
            ct:"例子A"
        }
    }
    doGetSecAsideList(content,e){
        for(var item of e.target.parentElement.children){
            item.style.background="";
            item.style.color="black";
            item.style.boxShadow="";
        }
        e.target.style.background="gray";
        e.target.style.color="white";
        e.target.style.boxShadow="0 0 6px black inset";
        this.setState({
            ct:content
        });
    }
    render(){
        return (
            <div id="section">
            
                 <SecAside getSecAsideList={this.doGetSecAsideList}/>
                <SecSection showSecSection={this.state.ct}/>
            </div>
        )
    }
}