
import React from "react"
import "./Header.css"
export default class Header extends React.Component{
    constructor(){
        super()
        this.state={

        }
    }

    render(){
        return (
            <div id="header">
                <img className="c_logo" src={require("../images/c_logo.jpg")}/> 
                <img className="t_logo" src={require("../images/t_logo.jpg")}/>
                <div className="p_right">
                    <div className="currentUser"><img className="currentUserImg p_right_xia_img" src={require("../images/current_user.png")}/><span>当前用户:</span><span>系统管理员(admin)</span></div>
                    <div className="exit"><img className="exitImg p_right_xia_img" src={require("../images/exit.png")}/><span>退出登录</span></div>
                </div>
            </div>
        )
    }
}