import React from "react"
import "./Paging.css"
export default class Paging extends React.Component{
    constructor(){
        super()
    }
    render(){
        return (
            <div id="paging">

                <div>
                    <span>&lt;&lt;</span>
                    <span>1</span>
                    <span>&gt;&gt;</span>
                </div>

                <div>
                    <button>转到</button>
                </div>

                <div>
                    <span>第</span><input type="text" value="1"/><span>页</span>
                </div>
                
                <div>共<span>2</span>条记录,第<span>1</span>/<span>1</span>页</div>

            </div>
        )
    }
}
