
import React from "react"
import "./SecAside.css"
export default class SecAside extends React.Component{
    constructor(){
        super()
        this.toUserScoreDetailsPage=this.toUserScoreDetailsPage.bind(this)
      
        this.state={
            num:1,
            list1:["例子A","例子B","员工信息表数据导入","数据源信息","命令DEMO","服务操作"],
            list3:["客户积分明细","客户积分赠送","客户积分兑换明细","客户积分兑换冲销"]
        }
    }
    componentDidMount(){
        
        var nv = document.getElementById('nav');
        var nvh=nv.offsetHeight;
        var hd = document.getElementById('header');
        var hbh=hd.offsetHeight;
        this.refs.sad.style .height=window.innerHeight-nvh-hbh+"px";
        var sl=document.querySelector(".scoreList>li:nth-child(1)");
        sl.style.background="gray";
        sl.style.color="white";
        sl.style.boxShadow="0 0 6px black inset";
        
        
    }
    componentDidUpdate(){

    }
    toUserScoreDetailsPage(){
        var sxb=document.getElementsByClassName("secSection_xia_box")[0];
        sxb.innerHTML=`<UserScoreDetails/>`;
    }
    
    render(){
        return (
            <div id="secAside" ref="sad" data-claName={"c1"} >
                <div className="c1">
                    <ul className="scoreList">
                    {this.state.list1.map((item,i)=>{
                        return (<li  onClick={(e)=>{this.props.getSecAsideList(item,e)}} key={i}><img src={require("./../../images/right.png")}/>{item}</li>)
                    })}
                        
                    </ul>
                </div>
                <div className="c3">
                    <ul className="scoreList">
                    {this.state.list3.map((item,i)=>{
                        return (<li onClick={(e)=>{this.props.getSecAsideList(item,e)}} key={i}><img src={require("./../../images/right.png")}/>{item}</li>)
                    })}
                    </ul>
                </div>
            </div>
        )
    }
}

